export default {
  data() {
    return {
      // 上传图片存储的路径
      pictureList: [],
      // 视频列表
      videoList: []
    };
  },
  methods: {
    // 点击上传图片
    upload() {
      if (this.pictureList.length < 9) {
        uni.chooseImage({
          count: 9,
          success: res => {
            // this.pictureList.push(res.tempFilePaths[0])
            console.log(res.tempFilePaths.length)
            for (let i = 0; i < res.tempFilePaths.length; i++) {
            this.pictureList.push(res.tempFilePaths[i])
            }
          }
        })
      }
    },
    // 点击预览图片
    preview(index) {
      uni.previewImage({
        urls: [this.pictureList[index]]
      })
    },
    // 长按删除
    handle(index) {
      uni.showModal({
        title: '提示',
        content: '是否确认删除该张照片',
        success: res => {
          if (res.confirm) {
              this.pictureList.splice(index, 1)
          } else if (res.cancel) {
              console.log('用户点击取消');
          }
        }
      })
    }
  }
};
